import React, { Component } from 'react'
import { Box, Footer, Logo } from './styles'

export default class Contact extends Component {
    render () {
        return (
            <Footer>
                <Box>
                    <Logo src={require('../../images/logo.svg')}/>
                    <p>&copy; 2019</p>
                </Box>
            </Footer>
        )
    }
}