import styled from 'styled-components'

const Box = styled.div`
    margin-top: 10px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;

    @media screen and (max-width: 600px) {
        flex-direction: column;
        justify-content: center;
        align-items: center;
    }
`
const Footer = styled.div`
    height: 100%;
    width: 100%;
    margin: 0;
    background-image: linear-gradient(45deg, #494949F2, #494949F2), url('https://images.pexels.com/photos/297642/pexels-photo-297642.jpeg?cs=srgb&dl=activity-adventure-blur-297642.jpg&fm=jpg?dl&fit=crop&crop=entropy&w=1920&h=1322e-sky-1054218.jpg&fm=jpg?dl&fit=crop&crop=entropy&w=1920&h=1280');
    background-position: center;
    background-repeat: no-repeat;
    font-family: 'Roboto', sans-serif;
    
    p {
        padding: 20px;
        font-weight: 600;
        color: #fff;
    }
`
const Logo = styled.img`
    width: 20%;
    height: 100%;
    margin-top: 50px;
    padding: 10px;
`

export { Box, Footer, Logo }