import React, { Component } from 'react'
import { 
    Header, 
    Container, 
    Box, 
    Content, 
    Title, 
    Text, 
    Image, 
    Toolbar, 
    ToolbarItem,
    ToolbarLogo,
    Button 
} from './styles'

import { IoIosArrowRoundForward } from 'react-icons/io'

export default class First extends Component {
    render () {
        return (
            <Header>
                <Toolbar>
                    <ToolbarLogo src={require('../../images/logo-w100.png')} />
                    <ToolbarItem href="https://gitlab.com/thaua97" target="_blank">Gitlab</ToolbarItem>
                    <ToolbarItem href="https://thaua97.github.io/portifolio/" target="_blank">Desenvolvedor</ToolbarItem>
                </Toolbar>   
                <Container>
                    <Box>
                        <Container className="tine">
                            <Title>Tudo em nossa volta<br/>
                            é espaço!</Title>
                            <Text>então toda viagem que fazemos é uma viagem espacial. O Space Travel é um app que visa auxiliar o usuário em sua viagem "espacial".</Text>
                            <Button href="https://gitlab.com/senac-tcc-2019/space-travel/wikis/home" target="_blank">Guia do projeto &nbsp; <IoIosArrowRoundForward size="35"/> </Button>
                        </Container>
                        <Content>
                            <Image src={require('../../images/trip.png')}/>
                        </Content>
                    </Box>
                </Container>
            </Header>
        )
    }
}
