import React, { Component }  from 'react'

import { Section, Container, Title, Text, Content, Box, Image } from './styles'

import { GiBackpack, GiSoccerField } from "react-icons/gi";
import { MdPalette } from 'react-icons/md'
import { WiDaySnowWind } from "react-icons/wi";

export default class First extends Component {
    render () {
        return (
            <Section>
                <Container>
                   <Box>
                        <Content>
                            <Title>Organize sua mala &nbsp; <GiBackpack size="40"/></Title>
                            <Text>De acordo com a sua estadia e o clima do local, o sistema lhe apresente uma forma inteligente de arrumar a sua mala sem excesso.</Text>
                            <Title>Preferências &nbsp;<MdPalette size="40"/></Title>
                            <Text>No intuito de melhorar sua experiência de viagem, você pode informar o seu gosto musical, lugares e comidas preferidas. </Text>
                        </Content>
                        <Image src={require('../../images/app.png')} />
                        <Content alignText="right">
                            <Title align="flex-end"><GiSoccerField size="40"/>&nbsp; Itinerário</Title>
                            <Text>De acordo com suas preferências o sistema vai indicar para você locais que pode ser do seu agrado para você visitar.</Text>
                            <Title align="flex-end"><WiDaySnowWind size="50"/>&nbsp; Seja precavido</Title>
                            <Text>Antes de viajar veja o clima do local, ajudando a verificar se a mala indicada está de acordo com a temperatura, para não haver surpresas.</Text>
                        </Content>
                   </Box>
                </Container>  
            </Section>
        )
    }
}